import { addressAbridge, cancelBubble } from '@cetus/utils'
import { Text, TextProps } from '@chakra-ui/react'

interface AddressUnderlineProps extends TextProps {
  address: string
  onClickLink: () => void
  hasUnderline?: boolean
  addressAbridgeStyle?: string
}
export default function AddressUnderline(props: AddressUnderlineProps) {
  const { address, hasUnderline = true, onClickLink, addressAbridgeStyle, ...rest } = props
  return (
    <Text
      cursor="pointer"
      color="text_paragraph"
      fontSize="12px"
      fontWeight="400"
      sx={{
        _hover: {
          textDecoration: hasUnderline ? 'underline' : 'none'
        }
      }}
      onClick={e => {
        cancelBubble(e)
        onClickLink()
      }}
      {...rest}
    >
      {addressAbridge(address, addressAbridgeStyle)}
    </Text>
  )
}
