import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { Center, HTMLChakraProps } from '@chakra-ui/react'
import copy from 'copy-to-clipboard'
import { useEffect, useState } from 'react'
import { useGlobalToast } from '../../hooks'

interface CopyButtonProps extends HTMLChakraProps<'div'> {
  text: string
  showRadiusBg?: boolean
  svgW?: string
  svgH?: string
}

export default function CopyButton(props: CopyButtonProps) {
  const { text, showRadiusBg = false, svgW = '20px', svgH = '20px' } = props
  const [isClick, setIsClick] = useState(false)
  const { showCommonToast } = useGlobalToast()
  const handleClick = (e: any) => {
    if (isClick) return
    cancelBubble(e)

    copy(text, {
      debug: false,
      message: 'message'
    })
    showCommonToast('Copied', 'success')

    setIsClick(!isClick)
  }

  useEffect(() => {
    if (isClick) {
      setTimeout(() => {
        setIsClick(false)
      }, 2000)
    }
  }, [isClick])

  if (showRadiusBg) {
    return (
      <Center w="32px" h="32px" bg="reward_modal_line" borderRadius="32px" cursor="pointer" onClick={handleClick}>
        <Icon w={svgW} h={svgH} svgW={svgW} svgH={svgH} xlinkHref={isClick ? '#icon-icon_tick' : '#icon-icon_copy'} />
      </Center>
    )
  }

  return (
    <>
      <Icon
        w={svgW}
        h={svgH}
        svgW={svgW}
        svgH={svgH}
        cursor="pointer"
        xlinkHref={isClick ? '#icon-icon_check' : '#icon-icon_copy'}
        onClick={handleClick}
      />
    </>
  )
}
